.article-swiper__item-date {
  font-weight: 500;
  font-size: 14px;
  line-height: 171%;
  color: #828282;
  margin-bottom: 8px;
}
.article-swiper__item-name {
  display: inline-block;
  font-weight: 500;
  font-size: 16px;
  line-height: 150%;
  text-decoration: underline;
  color: #171717;
  margin-bottom: 16px;
}
.article-swiper__item-link {
  margin-top: auto;
  width: fit-content;
}

.catalog__container {
  display: grid;
  grid-template-columns: 1fr 3fr;
  column-gap: 30px;
}
.catalog__title {
  margin-bottom: 24px;
}
.catalog__category {
  margin-bottom: 40px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 30px;
}
.catalog__category-item {
  border: 1px solid #DFDFDF;
  background-color: #ffffff;
  width: 100%;
  height: 172px;
  padding: 16px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  column-gap: 40px;
}
.catalog__category-item img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.catalog__category-image {
  width: 140px;
  height: 140px;
  flex-shrink: 0;
}
.catalog__category-image img {
  width: 100%;
  height: 100%;
}
.catalog__category-name {
  font-weight: 600;
  font-size: 16px;
  line-height: 150%;
  color: #171717;
}
.catalog__items {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
}
.catalog__pagination {
  grid-column: 1/4;
  margin-top: 10px;
}

.catalog-info__title {
  margin-bottom: 24px;
}
.catalog-info__text {
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 8;
  -webkit-box-orient: vertical;
}
.catalog-info__text.show {
  -webkit-line-clamp: inherit;
}
.catalog-info__button {
  font-size: 16px;
  line-height: 125%;
  text-decoration: underline;
  color: #828282;
  margin-top: 24px;
  position: relative;
  padding-right: 28px;
}
.catalog-info__button.hide {
  display: none;
}
.catalog-info__button::before {
  content: "";
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 20px;
  height: 20px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-image: url(../img/icons-arrow/arrow-text-down.svg);
}