.content {
    font-size: 16px;
    line-height: 200%;

    p {
        @include mb(16px);
    }

    strong,
    b {
        font-weight: 600;
        font-size: 20px;
        line-height: 24px;
        margin-bottom: 24px;

        &:not(:last-child) {
            margin-top: 40px;
        }
    }
}

