.form {
    display: flex;
    column-gap: 20px;

    &__title {
        margin-bottom: 12px;
    }

    &__subtitle {
        font-size: 20px;
        line-height: 24px;
        margin-bottom: 24px;
    }

    &__label {
        display: inline-block;
        width: 100%;
    }

    &__input {
        padding: 20px 24px;
        border-radius: 4px;
        background-color: $white;
        width: 100%;
        outline: none;
        height: 60px;

        &::placeholder {
            color: $grey;
        }
    }

    // &__textarea {
    //     padding: 16px 20px;
    //     border-radius: 4px;
    //     border: 1px solid $greyLine;
    //     background-color: $white;
    //     width: 100%;
    //     outline: none;
    //     @extend %regular16;
    //     height: 140px;
    //     flex-shrink: 0;
    //     resize: none;

    //     &::placeholder {
    //         color: $grey;
    //     }
    // }

    // &__attach {
    //     @include flexCStart;
    //     column-gap: 24px;
    //     margin-bottom: 20px;

    //     &-info {
    //         @extend %regular14;
    //         color: $grey2;
    //     }
    // }

    &__btn {
        border-radius: 4px;
        padding: 20px 48px;
        height: 60px;
        background-color: $green;

        font-weight: 600;
        font-size: 16px;
        line-height: 125%;
        color: $white;
        position: relative;
        @include transition(background-color);

        &::before {
            content: '';
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-340%, -50%);
            background-image: url(../img/icons/icon-send.svg);
            width: 20px;
            height: 20px;
            flex-shrink: 0;
        }

        &:hover {
            background-color: $green-light;
        }
    }

    &__descr {
        font-weight: 400;
        font-size: 15px;
        line-height: 18px;
        color: $grey;
        margin-top: 32px;

        a {
            text-decoration: underline;
            color: $grey;
        }
    }
}

